import React, { useEffect, useState } from "react";
import { RootState, RootDispatch } from "../../model";
import { useRematch } from "../../utils/useRematch";
import useInterval from "../../utils/useInterval";

import BoxMovie from "../../assets/box.mp4";
import T0 from "../../assets/bothead-png/T0.png";
import T1 from "../../assets/bothead-png/T1.png";
import T2 from "../../assets/bothead-png/T2.png";
import T3 from "../../assets/bothead-png/T3.png";
import T4 from "../../assets/bothead-png/T4.png";
import T5 from "../../assets/bothead-png/T5.png";

import "./index.less";

const imgList = [T0, T1, T2, T3, T4, T5];

const mapState = (state: RootState) => ({
  ...state.main,
  loading: state.loading,
});

const mapDispatch = (dispatch: RootDispatch) => ({
  ...dispatch.main,
});

export default function Index() {
  const [{ userInfo }, { setFields }] = useRematch(mapState, mapDispatch);

  const [imgIndex, setImgIndex] = useState(0);

  useInterval(() => {
    if (imgIndex < 5) {
      setImgIndex(imgIndex + 1);
    } else {
      setImgIndex(0);
    }
  }, 300);

  return (
    <div className="Index">
      <div className="bothead-show">
        {imgList.map((img, index) => {
          return (
            <img
              src={img}
              className={`bothead${imgIndex !== index ? " no-show" : ""}`}
              alt="bothead"
            />
          );
        })}
      </div>
      <div className="banner">
        <video
          autoPlay
          loop
          muted
          width="100%"
          className="banner-img"
          preload="auto"
        >
          <source src={BoxMovie} type="video/mp4" />
        </video>
      </div>
      <div className="open-mint">
        <div id="triangle"></div>
        <div className="open-mint-time-title">COMING SOON</div>
        {/* <div className="number-count">21:58:34</div> */}
        {/* <div className="number-count">COMMING SOON</div> */}
      </div>
      {/* <div className="mint-button">
      <Button></Button>
    </div> */}
    </div>
  );
}
